@import '../styles/responsive.scss';
@import '../styles/globals.scss';
@import '../index.scss';

$hover-color: #fcfa85;



.banner-container {
    --f-size: 15;
    --f-unit: 1vmin;
    --f: calc(var(--f-size) * var(--f-unit));

    --color-two: #000;
    --color-one: #fff;

    --color-three: $lighterpink;
    --color-four: rgb(92, 32, 255);
    // --color-two: #e9e9ff;
    // --color-one: #ffeaf7;
    --font-type: $atmos;

    // height: 100vh;
    width: 100vw;
    display: flex;
    flex-flow: column;
    // margin-top: 100px;
    // justify-content: center;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 20px;

    @media screen and (max-width: $lgDevice) {
        font-size: 3em;
    }

    .text-container {
        display: flex;
        flex-flow: column wrap;
        width: 100%;
        overflow: hidden;
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;

        .banner-item {
            font-weight: 900;
            font-family: $eight1;
            font-size: 6em;
            line-height: 1.2;
            position: relative;
            padding: 0;
            overflow: hidden;
            --bg-size: 200%;
            background: linear-gradient(90deg, var(--color-one), var(--color-two), var(--color-one)) 0 0/var(--bg-size) 100%;
            color: transparent;
            -webkit-background-clip: text;
            background-clip: text;
            // mix-blend-mode: hard-light;
            -webkit-animation: move-bg 8s infinite linear;
            animation: move-bg 8s infinite linear;

            .name {
                padding: 0;
                padding-right: 20px;
                position: relative;
                overflow: hidden;
                // font-family: $comfortaa;
                line-height: 1;
                --bg-size: 200%;
                text-shadow: -2px -2px 3px --color-one, 2px 2px 3px --color-two;
                background: linear-gradient(90deg, var(--color-one), var(--color-two), var(--color-one)) 0 0/var(--bg-size) 100%;
                color: transparent;
                -webkit-background-clip: text;
                background-clip: text;
                -webkit-animation: move-bg 8s infinite linear;
                animation: move-bg 8s infinite linear;
            }

            .role {
                color: #fff;
                padding-left: 0;
                position: relative;
                overflow: hidden;
                --bg-size: 200%;
                background: linear-gradient(152deg, var(--color-three), var(--color-four), var(--color-three)) 0 0/var(--bg-size) 100%;
                color: transparent;
                -webkit-background-clip: text;
                background-clip: text;
                -webkit-animation: move-bg 8s infinite linear;
                animation: move-bg 8s infinite linear;
            }

            @media screen and (max-width: $lgDevice) {
                font-size: 3.5rem;

                .banner-item {

                    --bg-size: 200%;
                    text-shadow: -2px -2px 3px --color-three, 2px 2px 3px --color-two;
                    background: linear-gradient(90deg, var(--color-three), var(--color-four), var(--color-one)) 0 0/var(--bg-size) 100%;
                    color: transparent;

                    .name {}

                }
            }
        }


        .underscore {
            width: 10px;
            color: #E80C7A;
            background-image: url('../assets/logos/droopy_e.png');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;
        }
    }

    .aboutSelfDeveloperContainer {
        font-size: 2em;
        color: #fff;
        padding-left: 0;
        position: relative;
        overflow: hidden;
        --bg-size: 200%;
        background: linear-gradient(152deg, var(--color-one), var(--color-two), var(--color-one)) 0 0/var(--bg-size) 100%;
        color: transparent;
        -webkit-background-clip: text;
        background-clip: text;
        -webkit-animation: move-bg 8s infinite linear;
        animation: move-bg 8s infinite linear;

        @media screen and (max-width: $lgDevice) {
            font-size: 2rem;
        }
    }

    @keyframes move-bg {
        to {
            background-position: var(--bg-size) 0;
        }
    }
}