@import './../styles/responsive.scss';
@import './../styles/globals.scss';
@import './../styles/mixins.scss';

@tailwind base;
@tailwind components;
@tailwind utilities;

.skillsetContainer {
    // width: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    align-items: flex-start;


    .skillset-background-image {
        position: absolute;
        // transform: rotate(-6deg);
        left: 0;
        width: 100%;
        // height: 440px;
        height: 10%;

        // background-image: url("../assets/images/code.jpeg");

        /* Add the blur effect */
        filter: blur(6px);

        /* Full height */

        /* Center and scale the image nicely */
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        z-index: 0;
    }

    .skillsHeader {
        font-size: 1.1rem;
        font-weight: 700;
        padding: 5px;
        padding-bottom: 10px;
        // text-decoration: underline $lighterpink;
        // text-decoration: underline $peachPuff;
    }

    .itemContainer {
        // width: 100%;
        z-index: 3;

    }

    .skillSetItem {
        width: fit-content;
        display: flex;
        flex-flow: column;
        justify-content: flex-start;
        align-items: flex-start;
        margin: 4px 0;
        box-sizing: border-box;
        border-radius: 10px;
        overflow: hidden;
        background-color: rgba(255, 255, 255, 0.7);

        h4 {
            // width: 100%;
            // font-weight: 900;
            padding: 10px 0 0 10px;
            margin: 0;
            // color: $green;
            color: $grayish;
            mix-blend-mode: add;
        }

        .skillItemsList {
            display: flex;
            flex-flow: row wrap;
            justify-content: flex-start;
            align-items: flex-start;
            overflow: hidden;
            font-size: small;
            padding: 5px;
            // background-color: $bkgrdPurple;
            // background-color: rgba(0, 0, 0, 0.4);

            .skillItem {
                font-size: 1rem;
                // background-color: $transBlack;
                margin: 0 10px;
                font-weight: 900;
                color: black;
            }

            .skillItem:nth-child(odd) {
                // color: $peachPuff;
                color: $bkgrdPurple
            }
        }

        @media only screen and (max-width: $lgDevice) {
            // width: 100%;
        }
    }
}