@import '../styles/responsive.scss';
@import '../styles/globals.scss';
@import '../index.scss';


.bannerMenu {
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    align-items: center;
    z-index: 0;
    padding: 0;
    line-height: 1;

    .bannerMenuItem {
        color: #fff;
        font-size: 2rem;
        padding: 0 30px;
        font-weight: 900;
        background-color: none;

    }

    .bannerMenuItem:hover {
        cursor: pointer;
    }

    @media screen and (max-width: $lgDevice) {
        display: flex;
        flex-flow: column;
        justify-content: flex-start;
        align-items: flex-start;
        padding: 0;

        .bannerMenuItem {
            font-size: 2rem;
            color: rgb(197, 255, 227);
            text-shadow: 2px 2px 2px rgba(255, 35, 163, 0.6), 1px 2px 2px rgba(0, 123, 255, 0.9);
            line-height: 1;
            padding: 20px 0;

            &:nth-child(2) {
                line-height: 1;
                padding-top: 30px;
            }

            @media screen and (orientation: landscape) {
                font-size: 1.8rem;

                &:nth-child(2) {
                    padding-top: 20px;
                    padding-left: 30px;
                }
            }
        }

        @media screen and (orientation: landscape) {
            // font-size: 1rem;
            display: flex;
            display: flex;
            flex-flow: row;
        }
    }
}