@import "../styles/globals.scss";
@import "../styles/responsive.scss";

.footerContainer {
    position: fixed;
    bottom: 20px;
    right: 1%;
    width: 100vw;
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-end;
    align-items: center;
    height: auto;
    overflow: visible;
    z-index: 100;
    // padding-top: 40px;
    // background-color: aqua;

    .linksContainer {
        z-index: 1000;
        display: flex;
        overflow: visible;

        .resumeText {
            align-self: center;
            text-align: end;
            font-weight: 800;
            font-size: 12px;
            -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
            margin-left: -20px;
        }

        a {
            display: flex;
            padding: 0 10px;
            color: $green;
            transition: all 500ms;

            &:hover {
                color: $pink;
                transform: rotate(10deg) scale(1.1);
            }
        }
    }


    .textItemContainer {
        padding: 10px;

        .textItem {
            font-size: 14px;
            color: $teal;
        }
    }

    @media screen and (max-width: $lgDevice) {

        color: rgb(255, 31, 135);

        .linksContainer {

            a {
                color: $bkgrdPurple;
                display: flex;
                padding: 10px;
                // color: rgb(255, 168, 208);
                transition: all 500ms;
            }
        }

        @media screen and (orientation: landscape) {
            bottom: 20px;
            right: 1%;
            width: 100px;
            height: 50vh;
            display: flex;
            flex-flow: row nowrap;

            align-items: flex-end;
            overflow: visible;
            z-index: 100;

            .linksContainer {
                z-index: 1000;
                display: flex;
                flex-flow: column;
                overflow: visible;

                .resumeText {
                    align-self: center;
                    text-align: end;
                    font-weight: 800;
                    font-size: 12px;
                    -webkit-transform: rotate(-90deg);
                    transform: rotate(-90deg);
                    margin-left: -20px;
                }

                a {
                    color: rgb(255, 168, 208);
                    display: flex;
                    padding: 10px;
                    // color: rgb(255, 168, 208);
                    transition: all 500ms;
                }
            }
        }
    }
}

.footerContainerInactive {
    position: fixed;
    bottom: 30px;
    right: 50px;
    display: flex;
    flex-flow: row;
    justify-content: flex-end;

    .inactiveItem {
        // width: 10px;
        // height: 10px;
        font-size: smaller;
        padding: 5px;
        border: solid 1px #ddd;
        border-radius: 2px;
        margin: 5px;
        background-color: #ddd;
    }
}